.loader {
  width: 8px;
  height: 40px;
  color: #000;
  box-sizing: border-box;
  background: currentColor;
  border-radius: 4px;
  margin: 120px auto;
  animation: animloader .3s linear .3s infinite alternate;
  display: block;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  width: 8px;
  height: 40px;
  box-sizing: border-box;
  background: currentColor;
  border-radius: 4px;
  animation: animloader .3s linear .45s infinite alternate;
  position: absolute;
  top: 50%;
  left: 20px;
  transform: translateY(-50%);
}

.loader:before {
  animation-delay: 0s;
  left: -20px;
}

@keyframes animloader {
  0% {
    height: 48px;
  }

  100% {
    height: 4px;
  }
}

.error {
  color: red;
  text-align: center;
  font-weight: bold;
  display: none;
}

.cat-info {
  text-align: center;
  background-color: #777;
  margin-top: 2rem;
  padding: 1rem;
  display: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, .3);
}

.cat-info img {
  width: 300px;
